type StorageSetCallback = () => void;
type StorageRemoveCallback = () => void;
type StorageGetCallback = (items: {[key: string]: any}) => void;
type StorageOnChangeCallback = (storageChange: chrome.storage.StorageChange) => void;

export class StorageService {

  set(key: string, value: any, callback: StorageSetCallback = () => {}) {
    chrome.storage.sync.set({[key]: value}, callback);
  }

  update(key: string, value: any, callback: StorageSetCallback = () => {}) {
    this.get(key, storage => {
      this.set(key, {...(storage || {}), ...value}, callback);
    });
  }

  get(key: string, callback: StorageGetCallback = () => {}) {
    chrome.storage.sync.get(key, result => callback(result[key]));
  }

  remove(key: string, callback: StorageRemoveCallback = () => {}) {
    chrome.storage.sync.remove(key, callback);
  }

  onChange(callback: StorageOnChangeCallback = () => {}) {
    chrome.storage.onChanged.addListener((storageChange: chrome.storage.StorageChange) => {
      callback(storageChange);
    });
  }
}
