import tabService from './tab.service';

type PopupCallback = (...args: any[]) => void;

interface IPopup {
  parentTab: chrome.tabs.Tab;
  popupTab: chrome.tabs.Tab;
  popupWindow: chrome.windows.Window,
}

class Popup implements IPopup {
  public popupTab: chrome.tabs.Tab;

  constructor(public parentTab: chrome.tabs.Tab, public popupWindow: chrome.windows.Window) {
    this.popupTab = popupWindow.tabs[0];
  }
}

export interface IPopupService {
  close(window: chrome.windows.Window, callback?: PopupCallback): void;
  focus(window: chrome.windows.Window, callback?: PopupCallback): void;
  open(createData: chrome.windows.CreateData, callback?: PopupCallback): void
}

export class PopupService implements IPopupService {

  public static defaultCreationData: chrome.windows.CreateData = {
    focused: true,
    height: 500,
    type: 'popup',
    width: 640,
  };
  public static defaultUpdateInfo: chrome.windows.UpdateInfo = {
    focused: true,
  };

  protected onCloseListeners: Map<number, PopupCallback[]>;
  protected popups: Set<IPopup>;

  constructor() {
    this.onCloseListeners = new Map<number, PopupCallback[]>();
    this.popups = new Set<IPopup>();

    chrome.tabs.onRemoved.addListener((tabId: number): void => {
      this.onClose(tabId);
    });
  }

  public close(window: chrome.windows.Window, callback?: PopupCallback): void {
    chrome.windows.remove(window.id, callback);
  }

  public focus(window: chrome.windows.Window, callback?: PopupCallback): void {
    tabService.getCurrentTab().then((): void => {
      chrome.windows.update(
        window.id,
        { ...PopupService.defaultUpdateInfo },
        (updatedWindow: chrome.windows.Window): void => {
          this.popups.forEach((popup: IPopup): void => {
            if (updatedWindow.id === popup.popupWindow.id) {
              Object.assign(popup.popupWindow, updatedWindow);
              callback(popup.popupWindow);
            }
          });
        },
      );
    });
  }

  public open(createData: chrome.windows.CreateData, callback?: PopupCallback): void {
    tabService.getCurrentTab().then((tab: chrome.tabs.Tab): void => {
      chrome.windows.create({
        ...PopupService.defaultCreationData,
        ...createData,
        tabId: tab.id,
      }, (window: chrome.windows.Window): void => {
        this.popups.add(new Popup(tab, window));
        callback(window);
      });
    });
  }

  public addCloseListener(window: chrome.windows.Window, callback: PopupCallback): void {
    if (this.onCloseListeners.has(window.id)) {
      this.onCloseListeners.get(window.id).push(callback);
    } else {
      this.onCloseListeners.set(window.id, [callback]);
    }
  }

  protected async isWindowExists(windowId: number): Promise<boolean> {
    return await new Promise<boolean>((resolve: (exists: boolean) => void) => {
      chrome.tabs.query({ windowType: 'popup', windowId }, (tabs: chrome.tabs.Tab[]): void => {
        resolve(!!tabs.length);
      });
    });
  }

  protected onClose(tabId: number): void {
    this.popups.forEach((popup: IPopup): void => {
      if (tabId === popup.parentTab.id || tabId === popup.popupTab.id) {
        const isParentTab: boolean = tabId === popup.parentTab.id;

        this.isWindowExists(popup.popupWindow.id).then((exists: boolean): void => {
          if (exists) {
            this.close(popup.popupWindow);
          }

          if (this.onCloseListeners.has(popup.popupWindow.id)) {

            if (!isParentTab) {
              this.onCloseListeners
                .get(popup.popupWindow.id)
                .forEach((callback: PopupCallback): void => callback(popup.popupWindow.id));
            }

            this.onCloseListeners.delete(popup.popupWindow.id);
          }

          this.popups.delete(popup);
        });
      }
    });

  }
}
