import {
  IdentityService,
  PopupService,
  TabService,
  StorageService,
} from './extension/extension.module.ts';

const originalFetch = window.fetch;
import fetch from 'fetch-retry';

const fetchRetry = fetch(originalFetch, {retries: 5, retryDelay: 1000});

let retryConnection = true;
let disableExtension = false;
const browserLanguage = chrome.i18n.getUILanguage();
const identityService = new IdentityService();
const popupService = new PopupService();
const tabService = new TabService();
const storageService = new StorageService();

if (!DEVELOPMENT_ENV) {
  Raven.config(RAVEN_URL, {
    release: RELEASE,
  }).install();
}

const EXTENSION_ACTIVE_REGEXS = [
  // all google docs
  /^https:\/\/docs\.google\.com\/document|spreadsheets|presentation|drawings\/d\/.+$/,
  // google drive
  /^https:\/\/drive\.google\.com\/file\/.+\/d\/.+$/,
  // google classroom without a calendar url
  /^https:\/\/classroom\.google\.com\/(?!calendar).*$/,
];

function getUninstallFormUrl(uiLanguage) {
  // Uninstall form url is different for Swedish language and rest of the world
  const languageBase = uiLanguage.toLowerCase().substring(0, 2);
  // Switch statement added for ease of extension
  switch (languageBase) {
    case 'sv':
      return 'https://docs.google.com/forms/d/e/1FAIpQLScPH19lWw3g6cR5JlpEQ68RixX1qBbnGn4NID_7mE7o6aJ3WQ/viewform';
    default:
      return 'https://docs.google.com/forms/d/e/1FAIpQLSeG9uz63PiS4Ifklb0-h6ef-W4ADlVfWwEs_R3x9MfNiRPwnQ/viewform';
  }
}

function checkForValidUrl(tab) {
  // disable extension means that we don't want to do any action
  if (disableExtension) return;

  return EXTENSION_ACTIVE_REGEXS.reduce(
    (active, regex) => active || tab.url.match(regex) !== null,
    false,
  );
}

function disabledExtensionAction() {
  chrome.tabs.query({currentWindow: true, active: true}, (tab) => {
    if (tab && tab.length) {
      disableExtension = true;
      chrome.pageAction.hide(tab[0].id);
    }
  });
}

const sendRequest = async (token, request, sender, sendResponse, authenticatedAjaxRepeat) => {
  const fetchRequest = {
    method: request.method,
    headers: {
      Authorization: `Token ${token}`,
      'Content-Type': 'application/json',
    },
  };
  if (request.method !== 'GET') {
    fetchRequest.body = JSON.stringify(request.data);
  }

  try {
    const response = await fetchRetry(request.url, fetchRequest);
    console.log('doFetch response: ', response);
    if (response.statusText === 'OK' || response.status === 200) {
      const json = await response.json();
      sendResponse({
        data: json,
        statusText: response.statusText,
        status: response.status,
      });
    } else if (response.status === 401 || response.status === 503) {
      // we got 401 again - let's disable extension
      if (!retryConnection) {
        disabledExtensionAction();
        return;
      }
      // Google API return 401 which may indicate that the cached access token was invalid
      // or Google API return 503 which means that service is unavailable.
      // Retry once with a fresh token.
      retryConnection = false;
      chrome.identity.removeCachedAuthToken(
        {token},
        authenticatedAjaxRepeat,
      );
    } else {
      sendResponse({
        data: null,
        statusText: response.statusText,
        status: response.status,
      });
    }
    retryConnection = true;
  } catch (e) {
    console.log('error: ', e.message);
    sendResponse({
      data: null,
      statusText: e.message,
      status: 0,
    });
  }
};

const refreshTokenTimeSeconds = 60 * 10;

function authenticatedAjax(request, sender, sendResponse) {
  const authenticatedAjaxRepeat = authenticatedAjax.bind(
    this,
    request,
    sender,
    sendResponse,
  );

  chrome.storage.sync.get('latestToken', (data) => {
    if (data && data.latestToken && data.latestToken.token) {
      const {latestToken} = data;
      const secondsOld = Math.floor((Date.now() - latestToken.date) / 1000);
      if (secondsOld > refreshTokenTimeSeconds) {
        chrome.storage.sync.remove('latestToken', () => {
          authenticatedAjax(request, sender, sendResponse);
        });
      } else {
        sendRequest(
          latestToken.token,
          request,
          sender,
          sendResponse,
          authenticatedAjaxRepeat,
        );
      }
    } else {
      chrome.identity.getAuthToken({interactive: true}, (token) => {
        chrome.storage.sync.set(
          {latestToken: {token, date: Date.now()}}, () => {
          });
        sendRequest(
          token,
          request,
          sender,
          sendResponse,
          authenticatedAjaxRepeat,
        );
      });
    }
  });
}

// event after install
chrome.runtime.onInstalled.addListener((details) => {
  if (details.reason === 'install') {
    chrome.tabs.create({url: 'pages/after-install-page.html'});
  }
});

// Setting url which will be loaded after extension is uninstalled
chrome.runtime.setUninstallURL(getUninstallFormUrl(browserLanguage));

// Fired when URL was changed
chrome.tabs.onUpdated.addListener((tabId, changeInfo, tab) => {
  // we only need this action for complete status
  // and new urls
  if (changeInfo.status === 'complete') {
    if (checkForValidUrl(tab)) {
      chrome.pageAction.show(tabId);
    }
  }
});

