/**
 * This is the trick to get an easy access to the URL changed event -
 * which is not possible from the contentscript.
 */
chrome.webNavigation.onHistoryStateUpdated.addListener((details) => {
  chrome.tabs.query({currentWindow: true, active: true}, (tab) => {
    if (tab && tab.length) {
      chrome.tabs.sendMessage(tab[0].id, {
        action: 'urlChanged',
        url: details.url,
      });
    }
  });
});

