chrome.pageAction.onClicked.addListener((tabs) => {
  chrome.tabs.sendMessage(tabs.id, {action: 'toggleStudybee'});
});

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
  if (request.action === 'openPopup') {
    popupService.open(request.createData, sendResponse);
  } else if (request.action === 'focusPopup') {
    popupService.focus(request.window, sendResponse);
  } else if (request.action === 'closePopup') {
    popupService.close(request.window, sendResponse);
  } else if (request.action === 'listenOnClosePopup') {
    popupService.addCloseListener(request.window, sendResponse);
  } else if (request.action === 'registerOnRemovedWindow') {
    chrome.windows.onRemoved.addListener(sendResponse);
  } else if (request.action === 'getCurrentTab') {
    tabService.getCurrentTab().then(sendResponse);
  } else if (request.action === 'registerOnRemovedTab') {
    tabService.addRemoveListener(sendResponse);
  } else if (request.action === 'openTab') {
    tabService.open(request.createProperties, sendResponse);
  } else if (request.action === 'focusTab') {
    tabService.focus(request.tabId, request.updateProperties, sendResponse);
  } else if (request.action === 'updateTab') {
    tabService.update(request.tabId, request.updateProperties, sendResponse);
  } else if (request.action === 'getTab') {
    sendResponse({ tabId: sender.tab.id });
  } else if (request.action === 'storageSet') {
    storageService.set(request.key, request.value, sendResponse);
  } else if (request.action === 'storageUpdate') {
    storageService.update(request.key, request.value, sendResponse);
  } else if (request.action === 'storageGet') {
    storageService.get(request.key, sendResponse);
  } else if (request.action === 'storageRemove') {
    storageService.remove(request.key, sendResponse);
  } else if (request.action === 'storageOnChange') {
    storageService.onChange(sendResponse);
  } else if (request.action === 'getUserInfo') {
    identityService.getUserInfo(sendResponse);
  }

  if (request.action === 'disableExtension') {
    disabledExtensionAction();
  }

  /**
   * This one is mostly for set up process,
   * before angular app is bootstrapped
   */
  if (request.action === 'getToken') {
    chrome.identity.getAuthToken(
      {
        interactive: true,
      },
      (token) => {
        if (
          chrome.runtime.lastError
          // this is shitty, but there is no other method the check that error
          // TODO check it for Swedish language
          && chrome.runtime.lastError.message
          === 'The user did not approve access.'
        ) {
          return;
        }

        // this is little trick to try to get token one more time
        // when you got `Authorization page could not be loaded.` error
        if (!token) {
          sendResponse({retry: true});
        } else {
          if (chrome.runtime.lastError) {
            // this is only to avoid stupid errors from chrome
            chrome.runtime.lastError.message;
          }
          sendResponse(token);
        }
      },
    );
  }

  if (request.action === 'fetchData') {
    authenticatedAjax(request, sender, sendResponse);
  }

  if (request.action === 'checkGDExportStatus') {
    const checkStatus = () => {
      authenticatedAjax(request, sender, (response) => {
        if (response.status !== 200) {
          sendResponse({state: 'FAILURE'});
        } else if (
          response.data.state === 'SUCCESS'
          || response.data.state === 'FAILURE'
        ) {
          sendResponse({state: response.data.state});
        } else {
          setTimeout(checkStatus, request.delay || 3000);
        }
      });
    };

    checkStatus();
  }

  return true;
});

