export type CreateTabCallback = (tab: chrome.tabs.Tab) => void;
export type UpdateTabCallback = (tab: chrome.tabs.Tab) => void;
export type GetTabCallback = (tab: chrome.tabs.Tab) => void;
export type RemoveTabCallback = (tabId: number, removeInfo: chrome.tabs.TabRemoveInfo) => void;

export class TabService {

  public static defaultUpdateProperties: chrome.tabs.UpdateProperties = {
    active: true,
  };

  public async open(createProperties: chrome.tabs.CreateProperties, callback?: CreateTabCallback): Promise<chrome.tabs.Tab> {
    const tab: chrome.tabs.Tab = await new Promise<chrome.tabs.Tab>((resolve: (tab: chrome.tabs.Tab) => void) => {
      chrome.tabs.create(createProperties, resolve);
    });

    if (callback instanceof Function) {
      callback(tab);
    }

    return tab;
  }

  public async focus(tabId: number, updateProperties: chrome.tabs.UpdateProperties, callback: UpdateTabCallback): Promise<chrome.tabs.Tab> {
    return this.update(tabId, { ...TabService.defaultUpdateProperties, ...updateProperties }, callback);
  }

  public async update(tabId: number, updateProperties: chrome.tabs.UpdateProperties, callback: UpdateTabCallback): Promise<chrome.tabs.Tab> {
    const tab: chrome.tabs.Tab = await new Promise<chrome.tabs.Tab>((resolve: (tab: chrome.tabs.Tab) => void) => {
      chrome.tabs.update(tabId, { ...updateProperties }, resolve);
    });

    if (callback instanceof Function) {
      callback(tab);
    }

    return tab;
  }

  public async get(tabId: number, callback: GetTabCallback): Promise<chrome.tabs.Tab> {
    const tab: chrome.tabs.Tab = await new Promise<chrome.tabs.Tab>((resolve: (tab: chrome.tabs.Tab) => void) => {
      chrome.tabs.get(tabId, resolve);
    });

    if (callback instanceof Function) {
      callback(tab);
    }

    return tab;
  }

  public async getCurrentTab(): Promise<chrome.tabs.Tab | null> {
    const queryInfo: chrome.tabs.QueryInfo = {
      active: true,
      currentWindow: true,
    };

    const tabs: chrome.tabs.Tab[] = await this.queryTab(queryInfo);

    return tabs[0] || null;
  }

  public addRemoveListener(callback: RemoveTabCallback): void {
    chrome.tabs.onRemoved.addListener(callback);
  }

  protected async queryTab(queryInfo: chrome.tabs.QueryInfo): Promise<chrome.tabs.Tab[]> {
    const tabs: chrome.tabs.Tab[] = await new Promise<chrome.tabs.Tab[]>((resolve: (tabs: chrome.tabs.Tab[]) => void) => {
      chrome.tabs.query(queryInfo, resolve);
    });

    return tabs && tabs.length ? tabs : [];
  }
}

const tabService: TabService = new TabService();
export default tabService;
